body {
    background-color: #2f2f2f;
    color: #fff;
    overflow: hidden;
    user-select: none;
}

.calendar {
    padding: 10px;
    height: 100vh;
    position: relative;
}

.calendar-container {
    width: 100%;
    max-width: 500px;
    margin: 50px auto;
    background-color: #e60000;
    border-radius: 10px;
    padding: 10px;
    text-align: center;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.calendar-container h1 {
    font-size: 7rem;
    margin-bottom: 10px;
    font-weight: bold;
}

.calendar-container p {
    border-radius: 8px;
    background-color: white;
    color: black;
    padding: 5px;
}

.calendar-container h2 {
    font-size: 2rem;
    margin-bottom: 10px;
    font-weight: bold;
}

.calendar-container small {
    margin-bottom: 10px;
    display: block;
}

.calendar-grid {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    grid-gap: 7px;
}

.calendar-day {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    background-color: #fff;
    color: #e60000;
    font-size: 1.4rem;
    font-weight: bold;
    border-radius: 8px;
}

.calendar-day-passed {
    color: grey;
    text-decoration: line-through;
}

.snowflakes {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    z-index: -1;
    overflow: hidden;
}

.snowflake {
    position: absolute;
    color: white;
    font-size: 1.5rem;
    animation: fall 10s linear infinite, sway 3s ease-in-out infinite;
    opacity: 0.8;
    top: -30px;
}


@keyframes fall {
    0% {
        left: calc(100% * var(--rand, 0.5));
    }

    100% {
        top: 100%;
        left: calc(100% * var(--rand, 0.5));
    }
}

@keyframes sway {

    0%,
    100% {
        transform: translateX(0);
    }

    50% {
        transform: translateX(10px);
    }
}